import '../../materialize/css/materialize.css';
import React, { useState, useEffect, Component } from 'react';
import '../Cadastro/style.css';
import Logo from '../../images/logo.png';
import api from '../../services/api';
import { login } from '../../services/auth';
import { Link } from 'react-router-dom';

export default function AlterarPerfil({ history }) {
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [name_artistic, setNome_artistic] = useState("");
    const [telephone, setCelular] = useState("");
    const [style_music, setStyle_music] = useState("");
    const [link_youtube, setYoutube] = useState("");
    const [link_whats, setWhats] = useState("");
    const [price_cache, setPrice] = useState("");
    const [category, setCategory] = useState("");
    const [img,setImg] = useState("");
    const views = "0";

    const USER = "@NOVAMUSIC:user";
    const [userLocal,setUserLocal]=useState([]);
    
    useEffect(()=>{
        const recebeUser=JSON.parse(localStorage.getItem(USER));
        setUserLocal(recebeUser);
        setEmail(userLocal.email);
    });
    
    async function alterar(event) {
        event.preventDefault();
        if ((email != "") && (name_artistic != "") && (telephone != "") && (style_music != "") && (link_youtube != "") && (link_whats != "")) {
            
            try {
                const user = {
                    email,
                    name_artistic,
                    telephone,
                    category,
                    style_music,
                    link_youtube,
                    link_whats,
                    price_cache,
                    views
                };
                    await api.put("/users/"+userLocal._id,user);
                    history.push("/login");
                
                
            } catch (erro) {
                console.log("Erro ao Cadastrar!");
            }
        } else alert("Existem campos vazios!");
    }
   
    const [list, setList] = useState([]);
    useEffect(() => {
        async function loadList() {
            const response = await api.get("/category");
            setList(response.data);
        }
        loadList();
    }, [list]);
    


    return (
        <div class="AlterarPerfil-container">
            <form class="form-profile" onSubmit={alterar}>
                <img id="logo-login" src={Logo}></img>
                <div class="container-form">
                    <label for="email_inline" >Email</label>
                    <input id="email_inline" type="email" class="validate"  onChange={event => setEmail(event.target.value)}></input>
                    <label for="nome_artistic_inline">Nome Artisitco</label>
                    <input id="nome_artistic_inline" type="text" class="validate" onChange={event => setNome_artistic(event.target.value)}></input>

                    <label for="celular_inline">Celular</label>
                    <input id="celular_inline" type="text" class="validate" onChange={event => setCelular(event.target.value)}></input>

                    <label for="youtube_inline">Link video YouTube</label>
                    <input id="youtube_inline" type="text" class="validate" onChange={event => setYoutube(event.target.value)}></input>

                    <label for="Whatsapp_inline">Link Whatsapp</label>
                    <input id="Whatsapp_inline" type="text" class="validate" onChange={event => setWhats(event.target.value)}></input>

                    <label for="price_cache_inline">Preço do cache por hora</label>
                    <input id="price_cache_inline" type="text" class="validate" placeholder="R$" onChange={event => setPrice("R$"+event.target.value)}></input>
                    <label htmlFor="cat">Categoria</label>
                    <select id="categoria" name="cat" onChange={event => setCategory(event.target.value)} className="browser-default">
                        <option value="" disabled selected>Escolha uma Categoria</option>
                        {list && list.map((category) => (
                            <option key={category._id} value={category._id}>
                                {category.name_category}
                            </option>
                        ))}

                    </select>
                    <label htmlFor="cat">Estilo musical</label>
                    <select id="categoria" name="cat" onChange={event => setStyle_music(event.target.value)} className="browser-default">
                        <option value="" disabled selected>Escolha um estilo musical</option>
                            <option value={"ROCK"}>Rock</option>
                            <option value={"SERTANEJO"}>SERTANEJO</option>
                            <option value={"GOSPEL"}>GOSPEL</option>
                            <option value={"FUNK"}>FUNK</option>
                            <option value={"POP"}>POP</option>
                            <option value={"POP/ROCK"}>POP/ROCK</option>
                            <option value={"POPULAR BRASILEIRA"}>POPULAR/BRASILEIRA</option>
                            <option value={"AXÉ"}>AXE</option>
                            <option value={"FORRO"}>FORRO</option>
                            <option value={"BOSSA NOVA"}>BOSSA NOVA</option>
                            <option value={"RAP"}>RAP</option>
                            <option value={"HIPHOP"}>HIPHOP</option>
                            <option value={"REAGGE"}>REAGGE</option>
                            <option value={"ELETRONICA"}>ELETRÔNICA</option>
                            <option value={"OUTROS"}>OUTROS</option>
                    </select>
                    <button id="btn-login" class="btn waves-effect waves-light" type="submit" name="action">Cadastrar
                    </button>
                    <Link to="/">
                        <button id="btn-cancelar" class="btn waves-effect waves-light" type="cancel" name="action">Cancelar
                        </button>
                    </Link>

                </div>
            </form>
        </div>
    );
}